import { Button } from '@consta/uikit/Button';
import { Text } from '@consta/uikit/Text';
import { Header, HeaderLogo, HeaderModule } from '@consta/uikit/Header';
import { Card } from '@consta/uikit/Card';
import styles from './styles.module.css';
import Logo from './mion.svg';
import Toggle from './night.png';
import Avatar1 from './ava1.png';
import Avatar2 from './ava2.png';
import Avatar3 from './ava3.png';
import Avatar4 from './ava4.png';
import Avatar5 from './ava5.png';
import Avatar6 from './ava6.png';
import Avatar7 from './ava7.png';
import IconFwd from './iconForward.png';
import { IconCopy } from '@consta/uikit/IconCopy';
import { IconForward } from '@consta/uikit/IconForward';
import { IconAdd } from '@consta/uikit/IconAdd';
import classNames from 'classnames';

export const LandingPage = ({ onClick, innerRef }) => (
  <>
    <div className={styles.wrapper} ref={innerRef}>
      <div className={styles.helloBlock}>
        <div className={styles.header}>
          <img src={Logo} className={styles.headerLogo}></img>
          <Text className={styles.headerItemChat}>Чат</Text>
          <Text className={styles.headerItemUser}>Личный кабинет</Text>
          <img src={Toggle} className={styles.headerToggle}></img>
        </div>
        <div className={styles.title}>
          <Text size="5xl" as="p" className={styles.titleText}>
            Единая консультационная платформа
          </Text>
          <Button
            label="Начать использовать"
            className={styles.titleBtn}
            onClick={onClick}
          />
        </div>
      </div>
      <div className={styles.mainBlock}>
        <Text className={styles.mainTitle}>Что такое единая платформа?</Text>
        <Text className={styles.mainText1}>
          Технологическое решение, объединившее экспертов и нейросеть. Единое
          консультационное окно 24/7. Платформа актуализирует данные ежедневно,
          используя достоверную информацию из внутренних систем компании.
          Максимальное время ответа составляет 2 минуты.
          <br />А еще — это дом виртуального помощника МИОН.
        </Text>
        <div className={styles.cardsContainer}>
          <div className={classNames(styles.card, styles.card1)}>
            <Text className={styles.cardTitle}>Стратегия</Text>
            <Text className={styles.cardText}>
              Экспертная оценка и предоставление информации по УПЦ,
              производственному контракту, финансово-операционным показателям и
              ключевым показателям эффективности
            </Text>
            <div className={styles.cardBottom}>
              <IconCopy className={styles.cardIconCopy} />
              <Text className={styles.cardBottomText}>Как это?</Text>
              <IconForward className={styles.cardIconForward} />
            </div>
          </div>
          <div className={classNames(styles.card, styles.card2)}>
            <Text className={styles.cardTitle}>Ресурс</Text>
            <Text className={styles.cardText}>
              Консультации, предоставление информации по товарно-материальным
              ценностям и нематериальным активам; по сотрудникам, их профилю
              деятельности, ставкам, % загрузки
            </Text>
            <div className={styles.cardBottom}>
              <IconCopy className={styles.cardIconCopy} />
              <Text className={styles.cardBottomText}>Ничего себе</Text>
              <IconForward className={styles.cardIconForward} />
            </div>
          </div>
          <div className={classNames(styles.card, styles.card3)}>
            <Text className={styles.cardTitle}>Менеджмент</Text>
            <Text className={styles.cardText}>
              Консалтинг, экспертная оценка и предоставление информации по
              бюджетам, закупкам, договорам, контрагентам, платежам
            </Text>
            <div className={styles.cardBottom}>
              <IconCopy className={styles.cardIconCopy} />
              <Text className={styles.cardBottomText}>Серьезно?!</Text>
              <IconForward className={styles.cardIconForward} />
            </div>
          </div>
        </div>
        <Text className={styles.mainTitle}>Зачем мне это?</Text>
        <Text className={styles.mainText1}>
          Чтобы получить актуальную и достоверную информацию в кратчайшие сроки
          для принятия качественных решений. Зачем чего-то ждать?
        </Text>
        <Text className={styles.mainTitle}>Как это работает?</Text>
        <Text className={styles.mainText1}>
          Виртуальный помощник МИОН взаимодействует с пользователем в диалоговом
          режиме. Формат позволяет получать информацию в привычном для человека
          виде. МИОН ничего не придумывает: он сообщает только точные данные из
          достоверных источников, которые прошли многоступенчатую проверку.
          Доступность данных соответствует уровню доступа пользователя. Если
          МИОН не найдёт информацию, то направит <u>запрос специалисту</u>.
        </Text>
        <Text className={styles.mainTitle}>История создания</Text>
        <Text className={styles.mainText1}>
          Конец 2021 года. Смотреть как команда тратит свое время на задачи,
          убивающие потенциал — сложно. Пожиратели драгоценного времени:
          множество однотипных запросов, разрозненные источники информации,
          непосредственный поиск информации.
          <br />
          Начало 2022 года. Невероятно удобная и комфортная среда. Информация
          доступна как никогда: человек способен помочь себе в решении
          поставленных задач гораздо быстрее и проще, чем любое поколение в
          истории… Мы объединяемся. Немного сомневаемся, но двигаемся вперёд в
          поисках новых возможностей.
          <br />
          Середина 2022 года. Решение найдено. Команда переезжает на платформу.
          <br />
          Конец 2022 года. К нам присоединяется волшебник по нейросетям.
          Платформа остается. Переезд команды отменяется. На платформу
          заселяется нейросеть. Защита проекта у идейного вдохновителя. 100%
          поддержка. Двигаемся дальше.
          <br />
          Начало 2023 года. Нейросеть учится. Мы учимся вместе с ней. Платформа
          создается. Названия пока нет. И тут рождается…«Мион» —
          структурно-функциональная единица скелетной мышцы человека. Отвечает
          за динамичные движения и статическую функцию…
          <br />
          Что скажете? Мы говорим: «Дa».
        </Text>
        <Text className={styles.mainTitle}>Команда проекта</Text>
        <Text className={classNames(styles.mainText1, styles.mainText3)}>
          Светлые головы, золотые руки
        </Text>
        <div className={styles.avatars}>
          <div className={styles.avatarContainer}>
            <img src={Avatar1} className={styles.avatar}></img>
            <img src={Avatar2} className={styles.avatar}></img>
            <img src={Avatar3} className={styles.avatar}></img>
            <img src={Avatar4} className={styles.avatar}></img>
            <img src={Avatar5} className={styles.avatar}></img>
            <img src={Avatar6} className={styles.avatar}></img>
            <img src={Avatar7} className={styles.avatar}></img>
          </div>
          {/* <IconForward className={styles.avatarFwd} /> */}
        </div>
        <Text className={classNames(styles.mainTitle, styles.mainTitle2)}>
          Примеры работы системы
        </Text>
        <div className={styles.bottomBlock}>
          <Text className={styles.mainText2}>
            Если Мион не найдет подходящий ответ?
          </Text>
          <IconAdd className={styles.bottomBlockAdd} />
        </div>
        <div className={styles.bottomBlock}>
          <Text className={styles.mainText2}>
            Как стать частью платформы? Интеграция
          </Text>
          <IconAdd className={styles.bottomBlockAdd} />
        </div>
        <div className={classNames(styles.bottomBlock, styles.bottomBlock2)}>
          <Text className={styles.mainText2}>А что еще может?</Text>
          <IconAdd className={styles.bottomBlockAdd} />
        </div>
        <hr className={styles.footerLine}></hr>
        <div className={styles.footer}>
          <Text className={styles.footerText}>
            МИОН. Менеджмент-консалтинг, 2023 г. Все права защищены.
          </Text>
          <Text className={styles.footerText}>
            Нарисовал А.Копылов. Построено с помощью Consta
          </Text>
        </div>
      </div>
    </div>
  </>
);