import { Text } from '@consta/uikit/Text';
import { Button } from '@consta/uikit/Button';
import { Card } from '@consta/uikit/Card';
import { IconEdit } from '@consta/icons/IconEdit';
import styles from './styles.module.css';
import Lisica from '../../LandingPage/ava5.png';
import File from './file.svg';
import { IconMoon } from '@consta/icons/IconMoon';

export const Cabinet = ({ innerRef }) => {
  return (
    <div className={styles.cabinet} ref={innerRef}>
      <div className={styles.topBarrier}>
        <Button label="Стратегия" view="clear" />
        <Button label="Ресурс" view="clear" />
        <Button label="Менеджмент" view="clear" />
        <Button
          view="clear"
          iconLeft={IconMoon}
          onlyIcon
          style={{ marginLeft: '4%' }}
        />
      </div>
      <Text className={styles.title}>Профиль</Text>
      <div className={styles.cards}>
        <Card className={styles.profileCard}>
          <div className={styles.avatarFlex}>
            <img src={Lisica} className={styles.avatar}></img>
            <Button
              view="clear"
              iconLeft={IconEdit}
              onlyIcon
              className={styles.iconEdit}
            />
            {/* <IconEdit className={styles.iconEdit} /> */}
          </div>
          <Text weight="semibold" className={styles.mainText}>
            Лисица Ольга Васильевна
          </Text>
          <Text className={styles.subText}>
            Занимаюсь вопросами по принятию решений, мониторингом задач,
            проверкой to do листов и сопутствующими вопросами
          </Text>
        </Card>
        <Card className={styles.filesCard}>
          <div className={styles.fileEllipse}>
            <img src={File} className={styles.fileIcon} />
          </div>
          <Text weight="semibold" className={styles.mainText}>
            Сохраненные файлы
          </Text>
          <Text className={styles.subText2}>
            Для вашего удобства, МИОН сохраняет все файлы из ваших чатов
          </Text>
          <div className={styles.btn}>
            <Button label="Посмотреть" form="round"></Button>
          </div>
        </Card>
      </div>
    </div>
  );
};
